<template>
	<view class="u-wrap">
		<view class="u-menu-wrap" >
			<scroll-view scroll-y scroll-with-animation class="u-tab-view menu-scroll-view" :scroll-top="scrollTop"
				:scroll-into-view="itemId">
				<view v-for="(item,index) in dataList" :key="index" class="u-tab-item"
					:class="[current == index ? 'u-tab-item-active' : '']" @tap.stop="swichMenu(index)">
					<text class="u-line-1">{{item.category}}</text>
				</view>
			</scroll-view>
			<scroll-view :scroll-top="scrollRightTop" scroll-y scroll-with-animation class="right-box"
				@scroll="rightScroll">
				<view class="page-view">
					<view class="class-item" :id="'item' + index" v-for="(item , index) in dataList" :key="index">
						<view class="item-title">
							<text>{{item.category}}</text>
						</view>
						<view class="item-container">
							<view class="thumb-box" v-for="(item1, index1) in item.foods" :key="index1">
								<image class="item-menu-image" :src="item1.foods_thumb" @click="toDetail(item1.name)" style="border-radius: 10rpx;"></image>
								<view style="display: flex; flex-direction: column; align-items: center;">
									<view class="item-menu-name">{{item1.name}}</view>
									<text class="total-price">
										￥{{ item1.price }}
									</text>
									<!-- 步进器 -->
									<u-number-box  input-width="40" v-model="item1.number" :index='item1.name' @change="valChange"></u-number-box>
									<!-- 价格 -->
									<!-- 共{{ value }}件餐品 -->
								</view>
							</view>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
		<dc_order></dc_order>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				show: true,
				resObj: {}, // 原始数据对象
				dataList: [], // 食物分类数据
				scrollTop: 0, //tab标题的滚动条位置
				oldScrollTop: 0,
				current: 0, // 预设当前项的值
				menuHeight: 0, // 左边菜单的高度
				menuItemHeight: 0, // 左边菜单item的高度
				itemId: '', // 栏目右边scroll-view用于滚动的id
				menuItemPos: [],
				arr: [],
				scrollRightTop: 0, // 右边栏目scroll-view的滚动条高度
				timer: null, // 定时器

			}
		},
		async onShow() {
			// 请求餐品数据
			let result = [
				{category: '',foods: []},
				{category: '',foods: []},
				{category: '',foods: []}
			]
			// 如果用户点击了步进器就请求 添加后的数据，相当于间接数据持久化
			let res =  this.FOODS
			let data = JSON.parse(JSON.stringify(res))
			this.resObj = data; // 存储一份原始数据，用来统计加购
			(data.result || []).forEach(res => {
				if(res.category == '今日特惠'){
				    result[0].category = res.category
				    result[0].foods.push(res)
				}
				if(res.category == '家常菜'){
				    result[1].category = res.category
				    result[1].foods.push(res)
				}
				if(res.category == '经济大菜'){
				    result[2].category = res.category
				    result[2].foods.push(res)
				}
			})
			this.dataList = result
			// // 请求餐品数据
			// await uniCloud.callFunction({
			// 	name: 'getFood',
			// 	success: (res) => {
			// 		let result = [
			// 			{category: '',foods: []},
			// 			{category: '',foods: []},
			// 			{category: '',foods: []}
			// 		]
			// 		let data = JSON.parse(JSON.stringify(res))
			// 		data.result.forEach(res => {
			// 			if(res.category == '今日特惠'){
			// 			    result[0].category = res.category
			// 			    result[0].foods.push(res)
			// 			}
			// 			if(res.category == '家常菜'){
			// 			    result[1].category = res.category
			// 			    result[1].foods.push(res)
			// 			}
			// 			if(res.category == '经济大菜'){
			// 			    result[2].category = res.category
			// 			    result[2].foods.push(res)
			// 			}
			// 		})
			// 		this.dataList = result
			// 		// console.log(this.dataList);
			// 	}
			// });
		},
		onReady() {
			this.getMenuItemTop()
		},
		onHide() {

		},
		methods: {
			// 跳转详情页
			toDetail(name) {
				uni.navigateTo({
					url: `/pages/detail/detail?name=${name}`
				})
			},
			// 步进器 并入加入购物车的对象
			valChange(e) {
				// 将加购数据同步到 onload请求的原始数据备份中
				this.resObj.result.forEach(res => {
					// 将数据的选中状态初始化
					// res.check = false
					// 同步加购的菜品数量
					if(res.name == e.index) {
						res.number = e.value
					}
				})
				// 合并数据
				this.$u.vuex('FOODS', this.resObj)
				// console.log(this.resObj);
			},
			
			// 点击左边的栏目切换
			async swichMenu(index) {
				if (this.arr.length == 0) {
					await this.getMenuItemTop();
				}
				if (index == this.current) return;
				this.scrollRightTop = this.oldScrollTop;
				this.$nextTick(function() {
					this.scrollRightTop = this.arr[index];
					this.current = index;
					this.leftMenuStatus(index);
				})
			},
			// 获取一个目标元素的高度
			getElRect(elClass, dataVal) {
				new Promise((resolve, reject) => {
					const query = uni.createSelectorQuery().in(this);
					query.select('.' + elClass).fields({
						size: true
					}, res => {
						// 如果节点尚未生成，res值为null，循环调用执行
						if (!res) {
							setTimeout(() => {
								this.getElRect(elClass);
							}, 10);
							return;
						}
						this[dataVal] = res.height;
						resolve();
					}).exec();
				})
			},
			// 观测元素相交状态
			async observer() {
				this.tabbar.map((val, index) => {
					let observer = uni.createIntersectionObserver(this);
					// 检测右边scroll-view的id为itemxx的元素与right-box的相交状态
					// 如果跟.right-box底部相交，就动态设置左边栏目的活动状态
					observer.relativeTo('.right-box', {
						top: 0
					}).observe('#item' + index, res => {
						if (res.intersectionRatio > 0) {
							let id = res.id.substring(4);
							this.leftMenuStatus(id);
						}
					})
				})
			},
			// 设置左边菜单的滚动状态
			async leftMenuStatus(index) {
				this.current = index;
				// 如果为0，意味着尚未初始化
				if (this.menuHeight == 0 || this.menuItemHeight == 0) {
					await this.getElRect('menu-scroll-view', 'menuHeight');
					await this.getElRect('u-tab-item', 'menuItemHeight');
				}
				// 将菜单活动item垂直居中
				this.scrollTop = index * this.menuItemHeight + this.menuItemHeight / 2 - this.menuHeight / 2;
			},
			// 获取右边菜单每个item到顶部的距离
			getMenuItemTop() {
				new Promise(resolve => {
					let selectorQuery = uni.createSelectorQuery();
					selectorQuery.selectAll('.class-item').boundingClientRect((rects) => {
						// 如果节点尚未生成，rects值为[](因为用selectAll，所以返回的是数组)，循环调用执行
						if (!rects.length) {
							setTimeout(() => {
								this.getMenuItemTop();
							}, 10);
							return;
						}
						rects.forEach((rect) => {
							// 这里减去rects[0].top，是因为第一项顶部可能不是贴到导航栏(比如有个搜索框的情况)
							this.arr.push(rect.top - rects[0].top);
							resolve();
						})
					}).exec()
				})
			},
			// 右边菜单滚动
			async rightScroll(e) {
				this.oldScrollTop = e.detail.scrollTop;
				if (this.arr.length == 0) {
					await this.getMenuItemTop();
				}
				if (this.timer) return;
				if (!this.menuHeight) {
					await this.getElRect('menu-scroll-view', 'menuHeight');
				}
				setTimeout(() => { // 节流
					this.timer = null;
					// scrollHeight为右边菜单垂直中点位置
					let scrollHeight = e.detail.scrollTop + this.menuHeight / 2;
					for (let i = 0; i < this.arr.length; i++) {
						let height1 = this.arr[i];
						let height2 = this.arr[i + 1];
						// 如果不存在height2，意味着数据循环已经到了最后一个，设置左边菜单为最后一项即可
						if (!height2 || scrollHeight >= height1 && scrollHeight < height2) {
							this.leftMenuStatus(i);
							return;
						}
					}
				}, 10)
			}
		}
		,
	}
</script>

<style lang="scss" scoped>
	.u-wrap {
		height: calc(100vh);
		/* #ifdef H5 */
		height: calc(100vh - var(--window-top) + 300rpx);
		/* #endif */
		display: flex;
		flex-direction: column;
	}
	.swiper_wrap {
		padding: 10rpx;
	}
	.search_d {
		padding: 4rpx 0;
		background-color: #f8f8f8;
	}
	.u-search-box {
		padding: 18rpx 30rpx;
	}

	.u-menu-wrap {
		background-color: #f8f8f8;
		flex: 1;
		display: flex;
		overflow: hidden;
	}

	.u-search-inner {
		background-color: #F8F8F8;
		border-radius: 100rpx;
		display: flex;
		align-items: center;
		padding: 10rpx 16rpx;
	}

	.u-search-text {
		font-size: 26rpx;
		color: $u-tips-color;
		margin-left: 10rpx;
	}

	.u-tab-view {
		width: 200rpx;
		height: 100%;
	}

	.u-tab-item {
		height: 110rpx;
		background: #F8F8F8;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 26rpx;
		color: #444;
		font-weight: 400;
		line-height: 1;
	}

	.u-tab-item-active {
		position: relative;
		color: #000;
		font-size: 30rpx;
		font-weight: 600;
		background: #fff;
	}

	.u-tab-item-active::before {
		content: "";
		position: absolute;
		border-left: 4px solid $u-type-primary;
		height: 32rpx;
		left: 0;
		top: 39rpx;
	}

	.u-tab-view {
		height: 100%;
	}

	.right-box {
		background-color: #F8F8F8;
	}

	.page-view {
		padding: 16rpx;
	}

	.class-item {
		margin-bottom: 30rpx;
		background-color: #f8f8f8;
		padding: 16rpx;
		border-radius: 8rpx;
	}

	.class-item:last-child {
		min-height: 100vh;
	}

	.item-title {
		font-size: 26rpx;
		color: $u-main-color;
		font-weight: bold;
	}

	.item-menu-name {
		font-weight: normal;
		font-size: 30rpx;
		margin-bottom: 6rpx;
		color: $u-main-color;
	}

	.item-container {
		display: flex;
		flex-wrap: wrap;
		background-color: #F8F8F8;
	}

	.thumb-box {
		background-color: #F8F8F8;
		width: 100%;
		padding: 6rpx;
		// box-shadow: 0 0 1rpx #000000;
		border-radius: 16rpx;
		display: flex;
		align-items: center;
		// justify-content: center;
		flex-direction: row;
		// align-items: flex-end;
		justify-content: space-around;
		margin-top: 20rpx;
	}

	.item-menu-image {
		width: 220rpx;
		height: 200rpx;
	}
</style>
